<template>
  <div class="RegisterByEle" @click.self="$router.push('/')">
    <div class="form-warpper">
      <el-row type="flex" class="row-bg" justify="center">
        <h1></h1>
      </el-row>
      <el-row type="flex" class="row-bg" justify="center">
        <!--  -->
        <el-col :span="24">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="userName">
              <el-input v-model="ruleForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="school">
              <el-cascader
                v-model="ruleForm.school"
                :options="schools"
                :props="{ expandTrigger: 'hover' }"
              ></el-cascader>
            </el-form-item>
            <el-form-item label="" prop="major">
              <el-cascader
                v-model="ruleForm.major"
                :options="majors"
                :props="{ expandTrigger: 'hover' }"
              ></el-cascader>
            </el-form-item>
            <el-form-item label="" prop="class">
              <el-select v-model="ruleForm.class" placeholder="">
                <el-option
                  v-for="(value, index) of classes"
                  :key="index"
                  :label="value"
                  :value="value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="sex">
              <el-select v-model="ruleForm.sex" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="ruleForm.email" type="email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input v-model="ruleForm.password" type="password"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"
                ></el-button
              >
              <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row type="flex" class="row-bg" justify="center">
        <div class="tip-wrapper">
          <router-link to="/LoginByEle"></router-link>
        </div>
      </el-row>
    </div>
  </div>
</template>

<script>
// 
import { schoolList } from "@/assets/js/chinaUniversityList.js";
import { majorList } from "@/assets/js/majorList.js";

// 
import { httpRegister } from "@/network/User.js";

export default {
  data() {
    return {
      ruleForm: {
        userName: "",
        school: "",
        major: "",
        class: "",
        email: "",
        password: "",
        sex: "",
      },
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        school: [{ required: true, message: "", trigger: "change" }],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/,
            message: "",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 8, message: " 6  8 ", trigger: "blur" },
        ],
        major: [{ required: true, message: "", trigger: "blur" }],
        class: [{ required: true, message: "", trigger: "blur" }],
        sex: [{ required: true, message: "", trigger: "blur" }],
      },
      schools: schoolList,
      majors: majorList,
      classes: [],
    };
  },
  created() {
    this.classes = this.SetClasses();
  },
  methods: {
    /**
     * 
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 
          httpRegister(this.ruleForm).then(
            (response) => {
              this.$message({
                message: ",",
                type: "success",
              });
            },
            (err) => {
              this.$message({
                message: "",
                type: "error",
              });
            }
          );
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    /**
     * 
     */
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    /**
     * 
     */
    SetClasses() {
      let date = new Date();
      let array = [];
      for (let i = date.getFullYear() - 4; i < date.getFullYear(); i++) {
        array.push(i + "");
      }
      return array;
    },
  },
};
</script>
<style lang="less">
.RegisterByEle {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(170, 162, 162, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .tip-wrapper {
    position: relative;
    font-size: 12px;
    text-align: center;
    margin-top: 10px;
  }
  .form-warpper {
    background: white;
    padding: 50px;
    box-shadow: 1px 1px 10px 10px rgba(0, 0, 0, 0.3);
    width: 40%;
    min-width: 400px;
  }
  .login-h1__center {
    text-align: center;
  }
}
</style>
